    /* src/components/Login.css */
    .login-container {
        max-width: 400px;
        margin: 80px auto;
        padding: 20px;
        border: 1px solid #ccc;
        border-radius: 5px;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    }
    
    .login-container h2 {
        text-align: center;
        margin: 30px 0;
    }
    
    .login-container form {
        display: flex;
        flex-direction: column;
    }
    
    .login-container div {
        margin-bottom: 15px;
    }
    
    .login-container label {
        margin-bottom: 30px;
        margin-left: 10px;
    }
    
    .login-container input {
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 4px;
        margin-left: 15px;
    }
    
    .login-container button {
        padding: 10px;
        background-color: #28a745;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
    }
    
    .login-container button:hover {
        background-color: #218838;
    }
    