/* AddTrack.css */
.main {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.addtrackone,
.addtracktwo {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
    margin: 50px;
}

.addtrack-container {
    background-color: white;
    padding: 30px;
    border-radius: 15px;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
    width: 450px;
    display: flex;
    flex-direction: column;
    position: relative;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.addtrack-container:hover {
    transform: scale(1.02);
    box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.3);
}

.addtrack-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.addtrack-title h2 {
    font-size: 28px;
    color: #333;
    font-family: "Arial", sans-serif;
}

.addtrack-title .cross {
    font-size: 24px;
    cursor: pointer;
    color: #ff5c5c;
    font-weight: bold;
    transition: color 0.3s ease;
}

.addtrack-title .cross:hover {
    color: #ff3b3b;
}

.addtrack-details label {
    font-size: 16px;
    margin-bottom: 8px;
    color: #49577e;
    font-weight: bold;
}

.addtrack-details input {
    width: 100%;
    padding: 12px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    font-size: 15px;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.addtrack-details input:focus {
    outline: none;
    border-color: #49577e;
    box-shadow: 0 0 8px rgba(73, 87, 126, 0.5);
}

.addtrack-container button {
    padding: 12px;
    background-color: #49577e;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

.addtrack-container button:hover {
    background-color: #3b4664;
    box-shadow: 0px 8px 15px rgba(59, 70, 100, 0.3);
}

.addtrack-container button:active {
    background-color: #2a3451;
    transform: translateY(2px);
}

.addtrack-container button:focus {
    outline: none;
}

/* Responsive adjustments */
@media (max-width: 480px) {
    .addtrack-container {
    width: 100%;
    padding: 20px;
    }

    .addtrack-title h2 {
    font-size: 24px;
    }

    .addtrack-title .cross {
    font-size: 20px;
    }

    .addtrack-details input {
    font-size: 14px;
    padding: 10px;
    }
}

