    display: flex;
    align-items: center;
    justify-content: center;
    height: 50%;
    width: 60%;
    background-color: rgb(5, 180, 5);
    color: white;
    font-weight: 600;
    border-radius: 5px;
}
.homestayPrize {
    display: flex;
    align-items: center;
    justify-content: center;
}
.prizeAll {
    margin: 1.2rem 1rem;
    color: black;
    font-size: 16px;
    width: 100%;
}

.prizeAll del {
    color: #888;
    margin-right: 10px;
}

.prizeAll .discount {
    color: rgb(10, 195, 10);
    font-size: 22px;
    font-weight: bold;
}

.homestaysButtons {
    margin: 30px 1rem;
    display: flex;
    justify-content: space-between;
}

.homestaysBtnone, .homestaysBtntwo {
    height: 2.7rem;
    width: 7rem;
    border-radius: 5px;
    border: 1px solid black;
    text-decoration: none;
    color: black;
    font-weight: bold;
    padding: 6.5px;
    transition: transform 0.3s ease, background-color 0.3s ease;
}

.homestaysBtntwo {
    background-color: rgb(9, 225, 37);
}

.homestaysBtnone:hover , .homestaysBtntwo:hover {
    transform: scale(1.1);
}
.itinerary_first {
    width: 85%;
    margin: auto;
    margin-top: 3rem;
}

.itinerary_grid {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    height: 100%;
}
.itinerary_pic img{
    border-radius: 5px;
    width: 95%;
}

.itinerary_elements {
    margin-top: 2rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-gap: 4rem;
    gap: 4rem;
}

.itinerary_heading h2 {
    color: black;
}
.same {
    font-size: 0.9rem;
}
.same img {
    width: 3rem;
}

.itinerary_second {
    width : 85%;
    margin:auto;
    margin-top: 5rem;
    display: grid;
    grid-template-columns: 2fr 1fr;
    min-height: 70rem;
    grid-gap: 2rem;
    gap: 2rem;
}
.overview_itinerary {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}
.itinerary_overview h1 {
    text-align: center;
    font-weight: bolder;
}
.itinerary_overview, .itinerary_highlight {
    padding: 2rem;
}
.itinerary_days_details h1 {
    text-align: center;
    font-weight: bolder;
    margin-bottom: 2rem;
}
.days {
    height: 3rem;
}
.days button {
    width: 100%;
    height: 100%;
    background-color: aliceblue;
}
.itinerary_highlight {
    background-color: rgb(227, 227, 227);
    border-radius: 5px;
    position: relative;
}
.scroll {
    background-color: white;
    min-height : 40%;
    padding: 1rem;
    border-radius: 5px;
    position: sticky;
    top: 5rem;
}
.highlight_pic {
    text-align: center;
    font-weight: bolder;
}
.highlight_pic img {
    width: 6rem;
}
.highlight_pic h2 {
    text-align: center;
    font-weight: bolder;
}
.itinerary_mid_banner {
    margin-top: 3rem;
    margin-bottom: 1rem;
    height: 25rem;
    width: 100%;
}

.itinerary_mid_banner img {
    height: 100%;
    width: 100%;
    object-fit: conver;
}
/* first div */
.firstItineraryHomestays {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 80%;
    margin: 4rem auto;
    grid-gap: 10px;
    gap: 10px;
}

/* images divided in three div */
.itsone, .itstwo, .itsthree {
    width: 100%;
    height: 100%;
}
.itsone img, .itstwo img, .itsthree img {
    height: 100%;
    width: 100%;
}
.itstwo, .itsthree {
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-gap: 10px;
    gap: 10px;
}

/* second section of homestays details */
.homestaysDetails {
    display: grid;
    grid-template-columns: 2fr 1fr;
    width: 80%;
    margin: 2rem auto;
    height: 20rem;
    grid-gap: 20px;
    gap: 20px;
}
.detailsOne {
    padding: 2rem;
    background-color: rgb(218, 248, 253);
    box-shadow: 2px 2px 5px ;
    border-radius: 5px;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}
.detailsOneFirst h2 {
    font-weight:bolder;
}
.detailsOneFirst p:nth-of-type(1) {
    color: blue;
}
.detailsOneFirst p:nth-of-type(2) {
    color: rgb(94, 93, 93);
}
.detailsOneSecond {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}
.detailsOneSecondFirst {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}
.detailsOneCheckIn p {
    background-color: white;
    color: rgb(17, 222, 17);
    width: 70%;
    border-radius: 5px;
}
.detailsOneCheckOut p {
    background-color: white;
    color: rgb(250, 153, 16);
    width: 70%;
    border-radius: 5px;
}
.detailsSecond {
    padding: 2rem;
    background-color: rgb(218, 248, 253);
    box-shadow: 2px 2px 5px ;
    border-radius: 5px;
    display: grid;
    grid-template-rows: 1fr 1fr 0.5fr;
}
.detailsSecondOne {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem;
    gap: 3rem;
}
.detailsSecondOneFirst h5 {
    font-weight: bold;
}
.detailsSecondOneFirst p {
    color: blue;
}
.detailsSecondOneSecond h5 {
    color: rgb(17, 222, 17);
}
.detailsSecondTwo {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem;
    gap: 3rem;
    margin-top: 1rem;
}
.detailsSecondThree {
    padding: 0.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem;
    gap: 3rem;
}

.detailsSecondThree button:nth-of-type(1) {
    border-radius: 10px;
    background-color: rgb(0, 162, 255);
    transition: transform 0.3s ease;
}
.detailsSecondThree button:nth-of-type(2) {
    border-radius: 10px;
    background-color: orange;
    transition: transform 0.3s ease;
}
.detailsSecondThree button:hover {
    transform: scale(1.1);
}

/* overview section */
.homestaysOverview {
    width: 80%;
    margin: 6rem auto;
    height: 20rem;
}
.homestaysOverview h2 {
    text-align: center;
    font-weight: 800;
}

.homestaysoverviewOuter {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 30px;
    gap: 30px;
    width: 100%;
    margin-top: 2rem;
}

.homestaysOverviewfirst img{
    width: 100%;
    border-radius: 5px;
}
.homestaysOverviewsecond {
    padding: 4rem;
}


/* why choose us section */

.homestaysSection {
    padding: 50px 20px;
    max-width: 1200px;
    margin: auto;
    text-align: center;
}

/* Cards Section */
.homestaysCards {
    display: flex;
    justify-content: space-between;
    grid-gap: 20px;
    gap: 20px;
    flex-wrap: wrap;
    margin-top: 30px;
}

.homestaysCard {
    background-color: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    flex: 1 1;
    min-width: 200px;
    transition: transform 0.3s, box-shadow 0.3s;
}

.homestaysCard:hover {
    transform: translateY(-10px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.homestaysCard svg {
    font-size: 2.5rem;
    color: #ff6600;
    margin-bottom: 10px;
}

/* Testimonials */
.homestaysTestimonials .homestaysTestimonial {
    background-color: white;
    padding: 20px;
    margin-top: 15px;
    border-radius: 12px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.1);
    font-style: italic;
}

.homestaysTestimonials p {
    margin-bottom: 5px;
}

/* Contact Form */
.homestaysContact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
    max-width: 600px;
    margin: auto;
}

.homestaysContact-form input,
.homestaysContact-form textarea {
    width: 100%;
    padding: 12px;
    border-radius: 8px;
    border: 1px solid #ccc;
    outline: none;
    transition: border-color 0.3s;
}

.homestaysContact-form input:focus,
.homestaysContact-form textarea:focus {
    border-color: #ff6600;
}

.homestaysContact-form button {
    background-color: #ff6600;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 25px;
    cursor: pointer;
    transition: background 0.3s;
}

.homestaysContact-form button:hover {
    background-color: #e55d00;
}


/* Contact Page Container */
.contact-page-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 40px auto;
    padding: 20px;
    max-width: 1200px;
    grid-gap: 5rem;
    gap: 5rem;
}

/* Form Section */
.form-container {
    flex: 1 1 45%;
    padding: 20px;
    background-color: #f8f9fa;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.form-container h1 {
    margin-bottom: 10px;
    font-size: 2rem;
    color: #333;
    text-align: center;
}

.contact-form {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 1rem;
}

.submit-button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.submit-button:hover {
    background-color: #0056b3;
}

/* Info Section */
.info-container {
    flex: 1 1 45%;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.info-container h2 {
    margin-top: 0;
    color: #333;
    text-align: center;
}

.info-container p {
    margin-bottom: 10px;
    font-size: 1rem;
    color: #555;
}

.social-links a {
    color: #007bff;
    text-decoration: none;
    margin-right: 10px;
}

.social-links a:hover {
    text-decoration: underline;
}

